<template>
  <div>
    <a
      v-for="job in jobs"
      :key="job.id"
      :href="job.href"
      :ref="`job_${job.shortcode}`"
      target="_blank"
      class="button button-outer job"
      @click="sendMetrika(job)"
    >
    <span v-for="(line, i) in title(job)" :key=i v-text=line></span>
    </a>
  </div>
</template>

<script>
import jobService from '../services/jobs.service'
/* eslint-disable no-irregular-whitespace */

const titleSort = (j1, j2) => {
  j1 = j1.title.toUpperCase();
  j2 = j2.title.toUpperCase();

  return (j1 > j2) ? 1
    : (j1 < j2) ? -1
    : 0;
}

export default {
  data: () => ({
    titleReplacers: {},
    jobs: [],
  }),

  computed: {
    replaceInTitle() {
      const replacements = Object.keys(this.titleReplacers)
      return title =>
        replacements
          .reduce(
            (t, key) => t.replace(new RegExp(key, 'g'), this.titleReplacers[key]),
            title
            );
    },
    title() {
      return ({ title, city, telecommuting }) => {
        const result = [
          title,
        ];
        if (city) result.push(`-Â ${city}`);
        if (telecommuting) result[1] += '/remote';
        return result.map(t => this.replaceInTitle(t));
      }
    }
  },

  methods: {
    sendMetrika({ title, location }) {
      window.ym.send('vacancy_click', null, {
        title,
        city: location.city,
        });
    },
  },

  async mounted() {
    const { replace = {} } = await window.getLoadedJSON('jobs');
    this.$set(this, 'titleReplacers', replace);

    const jobs = await jobService.getJobsList();
    this.$set(this, 'jobs', jobs.sort(titleSort));

    const showFn = el => () => el.classList.add('job-show')
    this.$nextTick(() => setTimeout(() =>
      Object.values(this.$refs).forEach(([ item ], i) =>
        // eslint-disable-next-line no-console
        setTimeout(showFn(item), 100 * i)
      )
    ), 2000);
  }
};
</script>

<style lang="scss">
  .job {
    display: none;

    &-show {
      display: block;
    }

    > span {
      display: block;
    }
  }
</style>