/* eslint-disable no-console */
import Content from '../../components/Content.vue'
import Jobs from '../../components/Jobs.vue'
import SocialLinks from '../../components/SocialLinks.vue'
import SideProjects from '../../components/SideProjects.vue'
import LinesExtended from "../../content/lines.extended";


export default {
  name: 'app',
  components: {
    Content,
    Jobs,
    SocialLinks,
    SideProjects
  },

  data: () => ({
    lines: { ...LinesExtended },
  }),

  methods: {
    sendMetrika(eventId) {
      window.ym.send(eventId);
    }
  },

  async mounted () {
    const isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
    if (isMobile)
      this.$router.push({ name: 'command', params: { command: 'home' } })
    const textLines = await window.getLoadedJSON('textLines');
    Object.keys(textLines).forEach(key =>
      this.$set(this.lines, key, textLines[key])
      );
  }
}