const fieldOf = (obj, path) => {
  const parts = path.split('.');
  if (parts.length === 1)
    return obj[path];
  let objPointer = obj;
  for (const part of parts) {
    if (false === part in objPointer)
      return void 0;
    objPointer = objPointer[part];
  }
  return objPointer
}

let inProgress;
let inProgressResolve = () => {};

export default {
  async getJobsList() {
    if (window.jobList)
      return Promise.resolve(window.jobList);
    if (inProgress)
      return inProgress;

    inProgress = new Promise(resolve => inProgressResolve = resolve);

    const { fetchUrl, linkFormat } = await window.getLoadedJSON('jobs');
    const jobs = [];

    if (window.whr) {
      window.whr(document).ready(() => {
        window.whr_embed(313923, {detail: 'titles', base: 'departments', zoom: 'city'});
        window.whrcallback = (data) => {
          if (!data || !data.jobs) {
            return inProgressResolve(jobs);
          }
          jobs.length = 0;
          data.jobs.forEach(j => {
            if (!j || j.department !== 'IT') return;
            j.href = linkFormat.replace(
              /(\{([\w\d_-]+)\})/ig,
              (_fullMatch, _replacement, field) => fieldOf(j, field),
            );
            jobs.push(j);
          });
          console.info('whr', { jobs });
          inProgressResolve(jobs)
        }
      });
    }

    await inProgress;
    // const makeRequest = async ({ token = null } = {}) => {
    //   const tokenObj = token ? { token } : {};
    //   const response = await fetch(fetchUrl, {
    //     method: 'POST',
    //     headers: { 'Content-Type': 'application/json' },
    //     body: JSON.stringify({
    //       query: '',
    //       ...tokenObj,
    //       location: [],
    //       department: ["IT"],
    //       worktype: [],
    //       remote: [],
    //     })
    //   });
    //   const { results, total, nextPage } = await response.json();
    //   results.forEach(job => {
    //     job.href = linkFormat.replace(
    //       /(\{([\w\d_-]+)\})/ig,
    //       (_fullMatch, _replacement, field) => fieldOf(job, field)
    //       );
    //     jobs.push(job)
    //   });
    //   if (total && nextPage && jobs.length < total)
    //     return makeRequest({ token: nextPage });
    // };
    // await makeRequest();

    window.jobList = jobs;
    return jobs;
  }
}